.blogPostContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.blogPostSubContainer {
  width: 90%;
}
.postTitle {
  font-size: 35px;
  font-weight: 500;
  text-align: center;
  font-family: Merriweather;
}
.postMeta {
  font-weight: normal;
  font-family: Merriweather;
  font-weight: 400;
  text-align: center;
  color: rgba(0, 0, 0, 0.54);
  font-size: 20px;
}
.author {
  margin: 30px 0;
  display: flex;
  font-family: Merriweather;
  align-items: center;
  color: rgba(0, 0, 0);
  display: flex;
  flex-direction: row;
  justify-content: center;
}
.author img {
  width: 65px;
  height: 65px;
  border-radius: 5px;
}
.author a {
  text-decoration: none;
  color: black;
  font-weight: 500;
  font-size: 18px;
  margin-right: 10px;
}
.publishedDate {
  color: rgba(0, 0, 0, 0.54);
  font-size: 16px;
  align-items: center;
  font-weight: 500;
}
.authorDescription {
  color: rgba(0, 0, 0, 0.75);
  font-size: 14px;
  align-items: center;
  font-weight: 500;
}
.author p {
  margin: 0;
  padding-top: 5px;
}
.postContent {
  width: 100%;
  font-family: Merriweather;
  font-size: 1.1rem;
  line-height: 1.5;
  color: rgba(0, 0, 0);
  font-weight: 400;
  margin: 75px 0 100px 0;
}
